:root {
  --app-width: 128rem;
  --slider-view: 6;
  --slider-gap: 1rem;
  --slider-item-width: calc(
    (100% - var(--slider-gap) * (var(--slider-view) - 1)) / var(--slider-view)
  );
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

input,
button {
  color: inherit;
  font: inherit;
  border: none;
  outline: none;
}

img,
video {
  vertical-align: bottom;
}

html {
  font-size: 62.5%;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", "Noto Sans KR", sans-serif;
  color: #23374d;
  font-size: 1.6rem;
}

#root {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  background: #fafafa;
}

/* 1024 */
@media (max-width: 64rem) {
  :root {
    --slider-view: 5;
  }
}

/* 768 */
@media (max-width: 48rem) {
  :root {
    --slider-view: 4;
  }
}

/* 480 */
@media (max-width: 30rem) {
  :root {
    --slider-view: 3;
  }
}

/* 320 */
@media (max-width: 20rem) {
  :root {
    --slider-view: 2;
  }
}

